<template xmlns:disable="http://www.w3.org/1999/xhtml">
  <div id="container">
    <div id="settings">
      <div class="user-pic">
        <img :src="$store.state.photo ? $store.state.photo : 'https://gold-ticket.uz/tgs/avatar.png'" :alt="$store.state.name">
      </div>
      <form class="settings-form" action="">
        <div class="form-control">
          <label class="form-label" for="name" v-if="$store.state.lang === 'ru'">Имя</label>
          <label class="form-label" for="name" v-else>Isim</label>
          <input class="form-input" id="name" name="name" v-model="name" type="text">
        </div>
        <div class="form-control">
          <label class="form-label" for="phone" v-if="$store.state.lang === 'ru'">Телефон</label>
          <label class="form-label" for="phone" v-else>Telefon</label>
          <input class="form-input" id="phone" name="phone" v-mask="'+998 (##) ###-##-##'" v-model="phone" :disabled="!!$store.state.phone" type="text">
        </div>
      </form>
    </div>
    <div id="saveButton" @click="userDataSave">{{ $store.state.lang === 'ru' ? 'Сохранить' : 'Saqlash' }}</div>
  </div>
</template>

<script>
export default {
  name: "Settings",
  data: () => ({
    name: '',
    phone: '',
    disable_btn: true,
  }),
  components: {
  },
  async mounted() {
    this.name = this.$store.state.name
    this.phone = this.$store.state.phone

    Telegram.WebApp.BackButton.show()
    Telegram.WebApp.BackButton.onClick(() => {
      Telegram.WebApp.MainButton.hide()
      this.$router.push('/profile')
    });
  },
  methods: {
    userDataSave: async function() {
      await this.$store.dispatch('updateUser', {
        user_id: Telegram.WebApp.initDataUnsafe.user.id,
        data: {
          name: this.name,
          phone: this.phone
        }
      })
    }
  }
}
</script>

<style scoped>
@import url('../style/settings.css');

#container {
  display: flex;
  justify-content: center;
  align-items: center;
}

@media screen and (min-height: 734px) {
  #container {
    width: 100%;
    height: auto;
    padding: 0 35px;
    margin-bottom: 0;
    position: relative;
    top: 90px;
  }
}

@media screen and (min-height: 700px) {
  #container {
    width: 100%;
    height: auto;
    padding: 0 35px;
    margin-bottom: 0;
    position: relative;
    top: 90px;
  }
}

@media screen and (min-height: 498px) {
  #container {
    width: 100%;
    height: auto;
    padding: 0 35px;
    margin-bottom: 200px;
    position: relative;
    top: 80px;
  }
}
</style>
